  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  background: none;
}

#new-pass-input {
  box-sizing: border-box;
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 40vh;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding-left: 20px;
  font-size: 16px;
}

#confirm-pass {
  position: absolute;
  width: 190px;
  height: 20px;
  left: 56vw;
  top: 51vh;
  background: none;
  font-family: "Montserrat";
