  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
}

#confirm-pass-input {
  box-sizing: border-box;
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 55.3vh;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding-left: 20px;
  font-size:16px;
}

#btn-reset {
  box-sizing: border-box;
  font-family: "Montserrat";
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 68vh;
  font-size: 16px;
  background: #6c63ff;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
}

#btn-reset:hover {
  background: #7971ff;
}

#btn-reset:active {
  transform: scale(0.99);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}
