#input-box {
  box-sizing: border-box;
  font-family: "Montserrat";
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 43vh;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding-left: 15px;
  outline-color: #6c63ff;
  background-color: white;
}

#no-otp-recieved {
  position: absolute;
  width: 55vw;
  height: 5vh;
  left: 56vw;
  top: 52vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: #000000;
  background: none;
}
#resend-otp {
  position: absolute;
  width: 55vw;
  height: 5vh;
  left: 42.8vw;
  top: 50.5vh;
  background: none;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 17px;
  border: none;
}
#resend-otp:hover{
  text-decoration: underline;
  cursor: pointer;
}
#btn-continue {
  box-sizing: border-box;
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 60vh;
  background: #6c63ff;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  letter-spacing: 1px;
  font-family: "Montserrat";
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
  color: white;
  cursor: pointer;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
}
#btn-continue:active {
  transform: scale(0.99);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

#OTPid {
  position: absolute;
  /* width: 20vw; */
  height: 7vh;
  left: 56vw;
  top: 38vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  background: none;
}
#timer{
  position: absolute;
  width: 2.4vw;
  height: 7vh;
  left: 74vw;
  top: 51.6vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 27px;
  line-height: 20px;
  color: #000000;
  background-color: white;
}
